<!---->
<template>
  <v-container dense>
    <v-card elevation="0" class="ma-1">
      <v-subheader></v-subheader>
      <v-divider class="divider_center"></v-divider>
      <!--  -->
      <v-row>
        <v-col cols="10">
          <v-row>
            <v-col cols="3">
              <v-subheader class="ma-0 mt-n5 pa-0"> </v-subheader>
            </v-col>
            <v-col cols="3">
              <v-subheader class="ma-0 mt-n5 pa-0"></v-subheader>
            </v-col>
            <v-col cols="2">
              <v-subheader class="ma-0 mt-n5 pa-0"
                ><small></small></v-subheader
              >
            </v-col>
            <v-col cols="2">
              <v-subheader class="ma-0 mt-n5 pa-0"></v-subheader>
            </v-col>
            <v-col cols="2">
              <v-subheader class="ma-0 mt-n5 pa-0"> </v-subheader>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="10">
          <v-row>
            <v-col cols="3">
              <div class="ma-0 mt-n5 pa-0 pl-4"></div>
            </v-col>
            <v-col cols="3">
              <div style="margin-top: -40px; padding: 0">
                <ph-2-date-picker
                  ref="date1"
                  width="100%"
                  @onChange="handleSproutDate"
                  style="margin: 0; padding: 0"
                  dense
                />
              </div>
            </v-col>
            <v-col cols="2">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                v-model.number="fruitValueSproutTreatment.weight"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                v-model.number="fruitValueSproutTreatment.count"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-btn
                class="primary ma-0 mt-n12 pl-1 pr-1"
                @click="saveUseFruitValueSproutTreatment"
                ></v-btn
              >
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="10">
          <v-row>
            <v-col cols="3">
              <div class="ma-0 mt-n5 pa-0 pl-4">E-L 2731</div>
            </v-col>
            <v-col cols="3">
              <div style="margin-top: -40px; padding: 0">
                <ph-2-date-picker
                  ref="date2"
                  width="100%"
                  @onChange="handleElDate"
                  style="margin: 0; padding: 0"
                  dense
                />
              </div>
            </v-col>
            <v-col cols="2">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                v-model.number="fruitValueELStage.weight"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                v-model.number="fruitValueELStage.count"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-btn
                class="primary ma-0 mt-n12 pl-1 pr-1"
                @click="saveUseFruitValueELStage"
                ></v-btn
              >
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="10">
          <v-row>
            <v-col cols="3">
              <div class="ma-0 mt-n5 pa-0 pl-4"></div>
            </v-col>
            <v-col cols="3">
              <div style="margin-top: -40px; padding: 0">
                <ph-2-date-picker
                  ref="date3"
                  width="100%"
                  @onChange="handleBaggageDate"
                  style="margin: 0; padding: 0"
                  dense
                />
              </div>
            </v-col>
            <v-col cols="2">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                v-model.number="fruitValueBagging.weight"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                v-model.number="fruitValueBagging.count"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-btn
                class="primary ma-0 mt-n12 pl-1 pr-1"
                @click="saveUseFruitValueBagging"
                ></v-btn
              >
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-divider class="divider_top" />
      <v-row>
        <v-col cols="3">
          <v-subheader> </v-subheader>
        </v-col>
        <v-col cols="2">
          <v-subheader>{{ fruitValues.burden }} </v-subheader>
        </v-col>
      </v-row>
      <v-divider class="divider_center" />
      <v-row>
        <v-col cols="3">
          <v-subheader> </v-subheader>
        </v-col>
        <v-col cols="2">
          <v-subheader>{{ fruitValues.amount }} </v-subheader>
        </v-col>
      </v-row>
      <v-divider class="divider_center" />
      <v-row>
        <v-col cols="3">
          <v-subheader>/ </v-subheader>
        </v-col>
        <v-col cols="2">
          <v-subheader>{{ fruitValues.count }} </v-subheader>
        </v-col>
      </v-row>
      <v-divider class="divider_bottom" />
    </v-card>
  </v-container>
</template>

<script>
import {
  useFruitValue,
  useFruitValues,
  useFruitValueSproutTreatment,
  useFruitValueELStage,
  useFruitValueBagging,
} from "@/api/TopStateGrowth/index";
import Ph2DatePicker from "@/components/parts/Ph2DatePicker.vue";
import moment from "moment";
// const fruitValuesDTOData = {
//   burden: 0.5,	//* 
//   amount: 0.8, //* g/mol
//   count: 0.4, //* //
// };

export default {
  props: {
    selectedMenu: Object,
    selectedField: Array,
    selectedYears: Array,
    selectedDevices: Array,
  },

  data() {
    return {
      devicesId: null, // ID
      year: null, //

      fruitValues: {
        burden: null, //* 
        amount: null, //* g/mol
        count: null, //* //
      },
      //
      fruitValueSproutTreatment: {
        date: moment().format("YYYY-MM-DD"), //
        weight: null, //
        count: null, //
      },
      //E-L 2731
      fruitValueELStage: {
        date: moment().format("YYYY-MM-DD"), //
        weight: null, //
        count: null, //
      },
      //
      fruitValueBagging: {
        date: moment().format("YYYY-MM-DD"), //
        weight: null, //
        count: null, //
      },

      //
      menu1: false,
      menu2: false,
      menu3: false,
    };
  },
  components: {
    Ph2DatePicker,
  },
  mounted() {
    this.getUseFruitValues();
  },
  methods: {
    initialize: function (menu) {
      this.devicesId = menu.selectedDevice.id;
      this.year = menu.selectedYear.id;
      this.$nextTick(function () {
        this.$refs.date1.initialize(menu.selectedYear);
        this.$refs.date2.initialize(menu.selectedYear);
        this.$refs.date3.initialize(menu.selectedYear);
      });
    },
    getFruitValue(eventId, field) {
      useFruitValue(this.devicesId, field.date, eventId)
        .then((response) => {
          //
          const { status, message, data } = response["data"];
          if (status === 0) {
            field.weight = null == data ? "" : data.average;
            field.count = null == data ? "" : data.count;
          } else {
            throw new Error(message);
          }
        })
        .catch((error) => {
          alert("");
          console.log(error);
        });
    },
    handleSproutDate(date) {
      if (null != date) this.fruitValueSproutTreatment.date = date;
      this.getFruitValue(1, this.fruitValueSproutTreatment);
    },
    handleElDate(date) {
      if (null != date) this.fruitValueELStage.date = date;
      this.getFruitValue(2, this.fruitValueELStage);
    },
    handleBaggageDate(date) {
      if (null != date) this.fruitValueBagging.date = date;
      this.getFruitValue(3, this.fruitValueBagging);
    },
    getUseFruitValues() {
      //
      useFruitValues(this.devicesId)
        .then((response) => {
          //
          const { status, message, data } = response["data"];
          if (status === 0) {
            this.fruitValues.burden =
              null == data.burden ? "" : data.burden;
            this.fruitValues.amount =
              null == data.amount ? "" : data.amount;
            this.fruitValues.count = null == data.count ? "" : data.count;
          } else {
            alert("");
            throw new Error(message);
          }
          console.log(this.devicesId);
          console.log(this.year);
        })
        .catch((error) => {
          //
          console.log(error);
        });
    },
    saveUseFruitValueSproutTreatment: function () {
      const data = {
        deviceId: parseInt(this.devicesId),
        year: this.year,
        eventId: null,
        ...this.fruitValueSproutTreatment,
      };
      console.log("saveUseFruitValueSproutTreatment", data);
      //
      useFruitValueSproutTreatment(data)
        .then((response) => {
          const { status, message } = response["data"];
          if (status === 0) {
            alert("");
            this.getUseFruitValues();
          } else {
            alert("");
            throw new Error(message);
          }
        })
        .catch((error) => {
          //
          console.log(error);
        });
    },
    saveUseFruitValueELStage: function () {
      const data = {
        deviceId: parseInt(this.devicesId),
        year: this.year,
        eventId: null,
        ...this.fruitValueELStage,
      };
      ////E-L 2731
      useFruitValueELStage(data)
        .then((response) => {
          const { status, message } = response["data"];
          if (status === 0) {
            alert("");
            this.getUseFruitValues();
          } else {
            alert("");
            throw new Error(message);
          }
        })
        .catch((error) => {
          //
          console.log(error);
        });
    },
    saveUseFruitValueBagging: function () {
      const data = {
        deviceId: parseInt(this.devicesId),
        year: this.year,
        eventId: null,
        ...this.fruitValueBagging,
      };
      //
      useFruitValueBagging(data)
        .then((response) => {
          const { status, message } = response["data"];
          if (status === 0) {
            alert("");
            this.getUseFruitValues();
          } else {
            alert("");
            throw new Error(message);
          }
        })
        .catch((error) => {
          //
          console.log(error);
        });
    },
    //
    updateTable() {
      this.getUseFruitValues();
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/style/common.css";

.divider_top {
  margin-bottom: 10px;
  color: "black";
}

.divider_center {
  margin-top: 10px;
  margin-bottom: 10px;
  color: "black";
}

.divider_bottom {
  margin-top: 10px;
  color: "black";
}

.text_field_size {
  min-width: 60px;
}
</style>
